/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { HashRouter as ReactRouter } from 'react-router-dom'
import { RecoilRoot, useSetRecoilState } from 'recoil'
import { UseHostedReturn } from '@namcbugdb/react-common'

import { AppRouter } from './components/AppRouter'
import { AppNotifications } from './components/AppNotifications'
import { GraphApi, apiState } from './data/api'

import './normalize.css'
import './App.css'

export interface AppProps {
  signOut: () => void
  auth: UseHostedReturn
}

const Component = ({ signOut, auth }: AppProps): JSX.Element => {
  const setApi = useSetRecoilState(apiState)
  const [ready, setIsReady] = useState(false)

  useEffect(() => {
    const apiInstance = new GraphApi(auth, signOut)
    setApi(apiInstance)
    setIsReady(true)
  }, [])

  if (!ready) return <div /> // wait until API is set

  return (
    <ReactRouter>
      <AppRouter />
      <AppNotifications />
    </ReactRouter>
  )
}

export const App = (props: AppProps): JSX.Element => {
  return (
    <RecoilRoot>
      <Component {...props} />
    </RecoilRoot>
  )
}
